.feed{
    display: flex;
    flex: 0.6 1;
    flex-direction: column;
    margin: 0px 30px;

}
.feed__form{
display: flex;
}
.feed .feed__input{
    display: flex;
    background-color: #fff;
    padding: 10px;
    border-radius: 10px;
   flex-direction: column;
}
.feed .feed__input form {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    flex: 1 1;
}
.feed .feed__input form input[type='text']{
    flex: 1 1;
    border-radius: 20px;
    padding: 0px 10px;
    margin-left: 10px;
    height: 40px;
    outline: 0;
    border: 1px solid #ccc;
}
.feed .feed__input form input[type='submit']{
    display: none;
}
.feed__options {
   display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
   
}
.feed__options .option{
    display: flex;
    align-items: center;
   color: #333;
   font-size: 14px;
   border-radius: 5px;
   padding: 0px 10px;
}

.feed__options .option:hover{
   background-color: #eee;
}

.feed__options .option svg{
margin-right: 5px;
}
.posts{
    background-color: #fff;
    padding: 10px;
    margin-top: 10px;
    border-radius: 10px;
}

.posts .posts__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.posts .posts__header .post__headerLeft{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.posts .posts__header .post__headerRight{
    display: flex;
    align-items: center;
 
}
.posts .posts__header .post_profile_details h3{
    font-size: 14px;
    margin: 0px;
    margin-left: 10px;
}

.posts .posts__header .post_profile_details p{
    font-size: 12px;
    margin: 0px;
    margin-left: 10px;
}

.post__body{
    font-size: 12px;
    padding: 20px 0px;
}

.post__footer{
    display: flex;
  
    align-items: center;
    border-top: 1px solid rgb(231, 231, 231);
    margin-top: 20px;
    padding: 10px;
}

.post__footer .post__footer__option{
    display: flex;
   cursor: pointer;
    align-items: center;
    margin-right: 20px;
}

.post__footer .post__footer__option svg{
   margin-right: 10px;
   color: #333;
}
.post__footer .post__footer__option span{
    color: #4e4d4d;
    font-size: 12px;
}
.header{
    display: flex;
    background-color: #fff;
    padding: 10px 15%;
    justify-content: space-between;
    height: 35px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    margin-bottom: 20px;
    z-index: 99;
}

.header__left{
    display: flex;
   
}
.header__left img {
   height: 30px;
   margin-right: 10px;

}
.header__search{
    display: flex;
    align-items: center;
    background-color: #eef3f8;
    padding: 3px 10px;
    border-radius: 5px;
}
.header__search input{
  background-color: transparent;border: 0px;
  outline: 0px;
  min-width: 220px;
}
.header__search svg{
 font-size: 20px;
 color: #333;
}
.header__right {
    display: flex;
    align-items: center;
}


.header__options {
    display: flex;
flex-direction: column;
align-items: center;
margin: 0px 15px;
font-size: 14px;
color: #565656;
cursor: pointer;
position: relative;
}
.header__options:first-child{
    color: #000;
}
.header__options:first-child::before{
    content: '';
    position: absolute;
    left: -10px;
    right: -10px;
    background-color: #333;
    height: 1px;
    bottom: -7px;
}

.header__options .MuiAvatar-circular {
    width: 30px;
    height: 30px;
    object-fit: contain;
}
.sidebar {
    display: flex;
    flex: 0.25 1;
    flex-direction: column;

}
.sidebar .sidebar__profile{
border-bottom: 1px solid #ccc;
text-align: center;
background-color: #fff;
border-radius: 10px;
padding-bottom: 10px;
overflow: hidden;
}

.sidebar .sidebar__profile img{
    width: 100%;
    height: 50px;
    margin-bottom: -30px;
}

.sidebar .sidebar__profile .MuiAvatar-circular{
 width: 50px;
 height: 50px;
 margin: 0px auto;
 object-fit: contain;
}
.profile__details{
  padding-bottom: 20px;
  border-bottom: 1px solid;
  margin-bottom: 20px;
}
.profile__details h4{
    margin-bottom: 0px;
}

.profile__stats{
     display: flex;
     justify-content: space-between;
     text-align: left;
     font-size: 12px;
     padding: 5px 10px;
     align-items: center;

}

.sidebar_recent{
    background-color: #fff;
    padding: 10px;
    margin: 10px 0px;
    border-radius: 10px;
    position: -webkit-sticky;
    position: sticky;
    top: 60px;
}
.sidebar_recent p{
   margin: 0px 0px 10px 0px;
   font-size: 13px;
   display: flex;
   
}

.hash span{
    font-weight: bold;
    font-size: 18px;
    margin-right: 5px;
    color: #333;
}
.widget{
    display: flex;
    flex-direction: column;
    flex: 0.3 1;
    border-radius: 10px;
    margin-bottom: 20px;
}

.widget__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.widget__header > h4 {
    margin: 0;
}
.widget__header svg {
    font-size: 18px;
}

ul.widget__options{
    margin: 0px;
    padding: 0px;
    padding-left: 15px;
    list-style: circle;
}
ul.widget__options li {
    margin-bottom: 10px;
}
.widget__body{
    margin-top: 10px;
}

ul.widget__options h4{
    margin: 0px;
    font-size: 15px;
    font-weight: normal;
}

ul.widget__options p{
    margin: 0px;
    font-size: 12px;
    color: #959595;
}
.widget__top{
    margin-bottom: 10px;
    background: #fff;
    padding: 10px;
    border-radius: 10px;
}
.loginScreen {
    width: 400px;
    margin: 100px auto;
    text-align: center;
}

.loginScreen img {
    width: 200px;
    margin-bottom: 20px;
}

.loginScreen input {
    display: block;
    height: 40px;
    width: 100%;
    margin-bottom: 10px;
    padding: 0px 10px;
    border: 2px solid #000;
    border-radius: 5px;
    box-sizing: border-box;
}

.loginScreen input[type="submit"] {
   background-color: #0077b5;
    border: 0;
    border-radius: 0px;
    color: #fff;
    letter-spacing: 2px;
    cursor: pointer;
}

.loginScreen h4 span{
    color: #0077b5;
    cursor: pointer;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  background-color: #f3f2ef;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app_body{
  width: 70%;
  padding: 0px 15%;
  display: flex;
}
